<template>
    <span v-if="index!==null">
      <Button type="text" @click="goIf">
        <span v-if="index!==-1">No.{{index+1}}&nbsp;&nbsp;&nbsp;&nbsp;</span>
         <span style="font-weight: bold;font-size: large;color:black">{{item.textTitle}}</span>
        ---  <Icon type="md-person" color="gray"/>{{item.user.userName}}
        <Icon type="ios-clock-outline" />{{item.textReleasedate}}
      </Button>
    </span>
    <span v-else>
      <Button type="text" @click="goElse"> {{item.text.textTitle}}---{{item.text.user.userName}}</Button>
    </span>
</template>

<script>
  export default {
    name: "BtCollections",
    inject:['reload'],
    data() {
      return {}
    }, methods: {
      goIf()
      {
        this.$router.push('/text?textId=' + this.item.textId);
        this.reload();
      },goElse(){
        this.$router.push('/text?textId='+this.item.text.textId);
       this.reload()
      }
    }, components: {}, mounted() {

    }, props: ['item', 'index']
  }
</script>

<style scoped>

</style>



