<template>
  <div class="header">
    <Row class="row" type="flex" justify="center" align="middle" >
      <Col span="1" offset="1">
        <router-link to="/">
          <img src="../../../static/img/logo.jpg" alt="" class="logo"/>
        </router-link>
      </Col>
      <Col span="4">
        <router-link to="/" class="logoText">博客平台</router-link>
      </Col>
      <Col span="4" offset="6">
        <Input search enter-button placeholder="根据题目搜索文章" v-model="searchTitle" @on-search="searchByTitle()" ></Input>
      </Col>
      <Col span="1" offset="1" >
        <Button type="text" @click="$router.push('/personHome/'+$store.state.userInfo.userId);reload()"
                v-if="this.$store.state.isLogin==true&&this.$store.state.userInfo.userHeadportrait!='default.jpg'">
          <Head-portrait :src="$store.state.userInfo.userHeadportrait"></Head-portrait>
        </Button>
        <Button type="text" @click="$router.push('/personHome/'+$store.state.userInfo.userId);reload()"
                v-else-if="this.$store.state.isLogin==true&&this.$store.state.userInfo.userHeadportrait=='default.jpg'">
          <img src="../../../static/img/default.jpg" class="round_icon"/>
        </Button>
        <router-link to="/login" v-else>
          <Avatar class="round_icon" icon="ios-person"/>
        </router-link>
      </Col>
      <Col span="1" class="drop" style="margin: 5px">
        <router-link to="/rank">文章排行</router-link>
      </Col>
      <Col span="2">
        <Dropdown  v-if="this.$store.state.isLogin">
          <a href="javascript:void(0)" class="drop">
            收藏夹
            <Icon type="ios-arrow-down"></Icon>
          </a>
          <DropdownMenu slot="list">
            <div v-if="isCollections">
              <DropdownItem v-for="(item,index) in collectList" :key="item.collectId">
                <Bt-go-text :item="item" :index="null" v-if="index<=4"></Bt-go-text>
                <Button v-else-if="index===5"
                        @click="$router.push('/personHome/'+$store.state.userInfo.userId+'/collectionsList')">查看更多收藏
                </Button>
              </DropdownItem>
            </div>
            <div v-else>
              收藏列表为空
            </div>
          </DropdownMenu>
        </Dropdown>
        <Dropdown   v-if="this.$store.state.isLogin">
          <a href="javascript:void(0)" class="drop">
            动态
            <Icon type="ios-arrow-down"></Icon>
          </a>
          <DropdownMenu slot="list">
            <Scroll>
              <div v-if="isFocus">
                <DropdownItem v-for="item in focusList" :key="item.textId">
                  <Bt-go-text :item="item" :index="-1"></Bt-go-text>
                </DropdownItem>
              </div>
              <div v-else>
                关注列表为空
              </div>
            </Scroll>
          </DropdownMenu>
        </Dropdown>
        <span v-else style="font-size: large;padding-top: 10px">  <router-link to="/login">请登录</router-link></span>
      </Col>
      <Col span="1" class="drop" style="margin:5px;" v-if="this.$store.state.isLogin">
        <a @click="logout" class="drop">退出登录</a>
      </Col>
    </Row>
  </div>
</template>

<script>
  import BtGoText from '../page/BtGoText'
  import HeadPortrait from '../page/HeadPortrait'

  export default {
    name: 'Header',
    inject:['reload'],
    data() {
      return {
        collectList: [],
        focusList: [],
        isCollections: false,
        size: 'large',
        isFocus: false,
        searchTitle:''
      }
    },
    methods: {
      getCollections() {
        this.axios.get('collect/' + this.$store.state.userInfo.userId).then(response => {
          if (response.data.code == 100) {
            this.isCollections = true
            this.collectList = response.data.map.userAllCollect.list
          } else {
            this.isCollections = false
          }
        })
      },
      logout() {
        this.$store.commit('setLogout')
        this.$router.push('/index')
      }, getFocus() {
        this.axios.get('focus/' + this.$store.state.userInfo.userId).then(response => {
          if (response.data.code == 100) {
            this.isFocus = true
            this.getFocusText()
          } else {
            this.isFocus = false
          }
        })
      }, getFocusText() {
        this.axios.get('focusText?focusUserid=' + this.$store.state.userInfo.userId).then(response => {
          if (response.data.code == 100) {
            this.focusList = response.data.map.getUserFocusText.list
          }
        }).catch(error => {
          this.$Message.warning("获取动态失败")
        })
      },searchByTitle(){
        if(this.searchTitle!==''){
          this.$router.push('/search/'+this.searchTitle)
          this.reload()
        }
      }
    },
    components: {
      BtGoText, HeadPortrait
    }, mounted() {
      if (this.$store.state.isLogin == true) {
        this.getFocus()
        this.getCollections()
      }
    }
  }
</script>

<style scoped>
  .logoText {
    color: gray;
    font-size: 30px;
    font-weight: bold;
  }

  .logo {
    height: 50px;
    width: auto;
    margin: 5px;
  }

  a {
    color: gray;
  }

  .row {
    margin-top: 10px;
  }

  .drop {
    font-size: large;
  }

  .round_icon:hover {
    transform: scale(1.4);
  }

  .round_icon {
    width: 50px;
    height: 50px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.6s;
  }

</style>



